@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
}

.leaflet-container {
  width: 100vw;
  height: 70vh;
  z-index: 0;
}

.ip-header {
  position: relative;
  height: 30vh;
  background-image: url('./images/pattern-bg.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ip-tracker-heading {
  color: white;
  font-weight: 500;
  font-size: 2rem;
}

.ip-search-container {
  display: flex;
  align-items: center;
  margin-top: 2rem;
  margin-bottom: 4rem;
}

.ip-searchbar {
  border-bottom-left-radius: 0.625rem;
  border-top-left-radius: 0.625rem;
  font-size: 1.125rem;
  padding: 1rem 20rem 1rem 1rem;
  border: none;
  cursor: pointer;
}

.ip-searchbar::placeholder {
  color: hsl(0, 0%, 59%);
}

.ip-searchbar:focus {
  border: none;
  outline: none;
}

.ip-search-button {
  border-bottom-right-radius: 0.625rem;
  border-top-right-radius: 0.625rem;
  border: none;
  background-color: black;
  height: 3.4rem;
  width: 4rem;
  transition: all 0.3s ease;
  cursor: pointer;
}

.ip-search-button:hover {
  background-color: hsl(0, 0%, 17%);
}

.user-data-parent-container {
  position: absolute;
  bottom: -4.375rem;
  z-index: 1;
  display: flex;
  background-color: white;
  border-radius: 0.625rem;
  padding: 2rem 6rem 2rem 2rem;
  gap: 0.1875rem;
}

.user-data-parent-container div:nth-last-child(2n+3) {
  padding-right: 3rem;
}

.user-data-parent-container div:nth-child(2n+3) {
  padding-left: 1rem;
}

.vertical-line {
  background-color: hsl(0, 0%, 59%);
  width: 0.09rem;
  max-height: 100%;
}

.user-heading {
  color: hsl(0, 0%, 59%);
  font-weight: 500;
}

.user-body {
  font-weight: 500;
  margin-top: 1rem;
  font-size: 1.25rem;
  color: hsl(0, 0%, 17%);
}